@import '~assets/styles/h3Imports';

.root {
  background-color: var(--card-bg, #ffffff);
  border-radius: var(--card-radius, 8px);
  box-shadow: var(--card-box-shadow, var(--box-shadow));
  display: flex;
  justify-content: space-between;
  overflow: hidden;
  position: relative;

  &[id='onboarding-7'] > figure {
    flex-basis: content;

    @media #{$large-up} {
      [data-fontsize='AAA'] & {
        padding-right: 70px;
      }
    }
  }
}

.content {
  display: flex;
  flex: 0 1 63.87%;
  flex-direction: column;
  justify-content: space-between;
  padding: em(16) 0 em(16) em(16);
  position: relative;
  z-index: 1;

  button {
    align-items: center;
    border: 2px solid var(--tertiary-button);
    border-radius: em(44);
    display: flex;
    font-size: em(14);
    height: em(45);
    line-height: em(20);
    margin: auto 0 0;
    min-width: unset;
    width: em(120, 14);
  }
}

.title {
  @include h4;

  color: var(--gray-5, #0a1414);
  font-size: em(19);
  font-style: normal;
  font-weight: bold;
  line-height: em(24);
  margin: 0;
}

.description {
  color: var(--gray-5, #0a1414);
  font-size: em(16);
  font-weight: 400;
  line-height: em(24);
  margin-top: em(8);
}

.image {
  display: flex;
  flex: 0 1 em(100);
  height: em(165);
  min-height: rem(160);
  width: em(100);

  @media #{$small-down} {
    [data-fontsize='AA'] & {
      svg {
        height: auto;
        width: 150%;
      }
    }

    [data-fontsize='AAA'] & {
      svg {
        height: auto;
        left: -45px;
        position: relative;
        width: 250%;
      }
    }
  }

  @media #{$large-up} {
    [data-fontsize='AAA'] & {
      svg {
        height: auto;
        left: 25px;
        position: relative;
        width: 150%;
      }
    }
  }

  img {
    object-fit: cover;
    object-position: right;
    width: 100%;
  }
}
